/*
A simple little SCSS mixin for creating scrim gradients
Inspired by Andreas Larson - https://github.com/larsenwork
https://css-tricks.com/easing-linear-gradients/
*/

body > .container-fluid {
  padding: 60px 15px 0;
}

/*
 * Typography
 */

h1,
h2,
h3,
h4,
h5,
h6 {
  text-transform: uppercase;
}

/*
 * Buttons
 */

.btn {
  text-transform: none;
  font-family: "Khand", sans-serif;
  font-weight: normal;
}

.btn.btn-facebook {
  background-color: #3B5998;
  color: #fffaf7;
}

.btn.btn-google {
  background-color: #EA4335;
  color: #fffaf7;
}

/*
 * Navbar
 */

.navbar {
  background-color: #28676b;
  color: #95c3c5;
}

.navbar .nav-link {
  text-transform: uppercase;
}

.navbar .nav-link.dropdown-toggle:after {
  content: none;
}

.navbar .navbar-right {
  flex-grow: unset;
}

.navbar .navbar-right .dropdown-menu {
  right: 0;
  left: auto;
}

.navbar .team-summary .badge {
  padding: .25rem .75rem;
}

.navbar .team-summary h5 {
  margin: 0;
}

.navbar .team-summary .summary-costs {
  position: relative;
  top: -1px;
  vertical-align: bottom;
  font-size: .9rem;
  font-weight: normal;
}

.navbar .team-summary .summary-costs img {
  filter: invert(1);
  height: 18px;
  width: 18px;
  position: relative;
  top: -1px;
}

.navbar .team-count-badge {
  color: #28676b;
  top: -2px;
  position: relative;
}

.navbar .profile-image {
  max-width: 4rem;
}

/*
 * Footer
 */

footer.footer-row {
  background-color: #472d13;
  position: absolute;
  bottom: 0;
  width: 100%;
  /* Set the fixed height of the footer here */
  height: 60px;
  line-height: 60px;
  /* Vertically center the text there */
}

footer.footer-row > .container {
  padding-right: 15px;
  padding-left: 15px;
}

/*
 * Sticky footer styles
 */

html {
  position: relative;
  min-height: 100%;
}

body {
  /* Margin bottom by footer height */
  margin-bottom: 60px;
}

/*
 * Backgrounds
 */

@keyframes animatedBackground {
  from {
    background-position: 0 0;
  }

  to {
    background-position: 0 2000px;
  }
}

body.bright-bg {
  background-color: #BF7733;
